import Vue from 'vue'

let StorageUtil = {
  setItem: (key, val) => {
    localStorage.setItem(key, JSON.stringify(val))
  },
  getItem: (key) => {
    return localStorage.getItem(key) ? JSON.parse(localStorage.getItem(key)) : null
  },
  removerItem: (key) => {
    localStorage.removeItem(key)
  }
}

let UrlUtil = {
  /**
   * 获取请求参数
   * @param name 参数名称
   * @returns {*}
   */
  getParam (name) {
    let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
    let r = window.location.search.substr(1).match(reg)

    if (r != null) {
      return decodeURIComponent(r[2])
    }
    return null
  }
}

let ObjectUtil = {
  /**
   * 克隆对象
   * @param obj 被克隆的对象
   * @returns {*} 克隆后的对象
   */
  cloneObject: (obj) => {
    let str = ''
    let newobj = obj.constructor === Array ? [] : {}
    if (typeof obj !== 'object') {
      return
    } else if (window.JSON) {
      str = JSON.stringify(obj) // 序列化对象
      newobj = JSON.parse(str) // 还原
    } else {
      for (let i in obj) {
        newobj[i] = typeof obj[i] === 'object' ? this.cloneObject(obj[i]) : obj[i]
      }
    }
    return newobj
  },
  isEmpty: (obj) => {
    if (obj) {
      let index = 0
      for (let key in obj) {
        if (key) {

        }
        ++index
      }
      if (!index) {
        return true
      }
      return false
    } else {
      return true
    }
  }
}

Vue.prototype.$SinoiovUtils = {
  StorageUtil,
  ObjectUtil,
  UrlUtil
}

export default {
  StorageUtil,
  UrlUtil,
  ObjectUtil
}



