import Vue from 'vue'
import SinoiovUtils from '../expand/vue-expand'
import base64 from 'base-64'
import utf8 from 'utf8'
/**
 * 全局回调中转函数
 */
let globalCallbackOfCurrentPosition
let globalCallbackOfRightIconClick
let globalCallbackOfGetPicture

const PLATFORM_DESKTOP = 'desktop'
const PLATFORM_ANDROID = 'android'
const PLATFORM_IOS = 'ios'

let appBridge = {
  nativeToH5: {
    nativeCallback (method, value) {
      switch (method) {
        case 'currentPositionCallback':
          this._callbacks._currentPositionCallback(value)
          break
        case 'rightIconClickCallback':
          this._callbacks._rightIconClickCallback(value)
          break
        case 'getPictureCallback':
          this._callbacks._getPictureCallback(value)
      }
    },
    _callbacks: {
      /**
       * 当前位置 callback
       * @param point
       * point: {longitude, latitude}
       */
      _currentPositionCallback (point) {
        if (typeof point === 'string') {
          point = JSON.parse(point)
        }

        if ((!point.longitude || point.longitude === 'null' || point.longitude === 'undefined') || (!point.latitude || point.latitude === 'null' || point.latitude === 'undefined')) {
          point.longitude = ''
          point.latitude = ''
        }

        globalCallbackOfCurrentPosition && globalCallbackOfCurrentPosition(point)
        globalCallbackOfCurrentPosition = null // 这句代码非常重要，否则回调会被调用多次
      },
      /**
       * 右边icon点击 callback
       * @param phones
       */
      _rightIconClickCallback (phones) {
        if (typeof phones === 'string') {
          phones = JSON.parse(phones)
        }
        globalCallbackOfRightIconClick && globalCallbackOfRightIconClick(phones)
        globalCallbackOfRightIconClick = null
      },
      /**
       * 拍照回调
       * @param pictureUrl 照片URL
       */
      _getPictureCallback (pictureUrl) {
        // 兼容IOS，IOS只能回传JSON形式的字符串
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          if (pictureUrl) {

          }
          pictureUrl = pictureUrl.imageBase64
        }
        globalCallbackOfGetPicture && globalCallbackOfGetPicture(pictureUrl)
        globalCallbackOfGetPicture = null
      }
    }
  },
  h5ToNative: {
    /**
     * 获取基本信息
     * @returns {*}
     */
    getBaseInfo () {
      let baseInfo = window.appNative ? window.appNative.getBaseInfo() : SinoiovUtils.StorageUtil.getItem('baseInfo')
      if (baseInfo) {
        if (typeof baseInfo === 'string') {
          return JSON.parse(baseInfo)
        } else {
          return baseInfo
        }
      }
      return null
    },
    /**
     * token失效
     * @param errorMessage 失效后给予用户提供的信息
     */
    tokenInvalid (errorMessage) {
      let options = {
        errorMessage
      }

      if (bridgeUtil.Platform.isPlatform(PLATFORM_DESKTOP)) {
        this.page.openPage({
          page: 'login'
        })
      }

      if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
        window.appNative.tokenInvalid()
      }
      if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
        let encoded = bridgeUtil.Base64.getBase64String(options)
        window.location.href = `native:tokenInvalid:${encoded}`
      }
    },
    header: {
      setHeader (options) {
        for (let key in options) {
          if (key === 'right') {
            options[key].forEach((right) => {
              let encoded = bridgeUtil.Base64.getBase64String(right.params)
              if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
                right.callback = 'rightIconClickCallback'
              }

              if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
                right.callback = `native:callback:rightIconClickCallback:${encoded}`
              }

              globalCallbackOfRightIconClick = right.callbackLocal
            })
          }
        }

        let nativeOptions = SinoiovUtils.ObjectUtil.cloneObject(options)
        for (let key in nativeOptions) {
          if (key === 'right') {
            nativeOptions[key].forEach((right) => {
              delete right.callbackLocal
            })
          }
        }

        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.setHeader(JSON.stringify(nativeOptions))
        }

        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(nativeOptions)
          window.location.href = `native:header:${encoded}`
        }
      }
    },
    page: {
      /**
       * 打开html5页面
       * @param options
       */
      openPage (options) {
        if (!options || !options.page) {
          return
        }

        let href = window.location.href
        options.url = `${href.substring(0, href.lastIndexOf('/') + 1)}${options.page}.html`
        delete options.page

        if (options.params) {
          let queryParams = []
          let queryParamsStr = ''

          for (let paramName in options.params) {
            queryParams.push(paramName + '=' + options.params[paramName])
          }
          queryParams.forEach((param, index) => {
            if (index === 0) {
              queryParamsStr += '?'
            }
            queryParamsStr += param + '&'
          })

          queryParamsStr = queryParamsStr.substring(0, queryParamsStr.length - 1)
          options.url = `${options.url}${queryParamsStr}`

          delete options.params
        }

        if (bridgeUtil.Platform.isPlatform(PLATFORM_DESKTOP)) {
          window.location.href = options.url
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.openPage(JSON.stringify(options))
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(options)
          window.location.href = `native:openPage:${encoded}`
        }
      },
      /**
       * 打开原生界面
       * @param page
       * @param params
       */
      openNativePage (params) {
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.openNativePage(JSON.stringify(params))
        }

        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(params)
          window.location.href = `native:openNativePage:${encoded}`
        }
      },
      /**
       * 刷新页面
       * @param page
       */
      refreshPage (page = '') {
        let options = {
          page
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.refreshPage(JSON.stringify(options))
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(options)
          window.location.href = `native:refreshPage:${encoded}`
        }
      },
      /**
       * 关闭页面
       */
      closePage () {
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.closePage()
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          window.location.href = 'native:closePage:'
        }
      },
      /**
       * 跳转页面
       * @param page  页面名称
       */
      skipPage (page) {
        let options = {
          page
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.skipPage(JSON.stringify(options))
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(options)
          window.location.href = `native:skipPage:${encoded}`
        }
      }
    },
    camera: {
      /**
       * @param callback
       * @param options
       *  sourceType: type-number;default-2;description-0:拍照，1:相册，2:两者都有
       *  allowEdit:type-boolean;default-false;description-是否允许编辑
       *  hasWatermark:type-boolean;default-false;description-是否含有水印
       */
      getPicture (callback, options = {sourceType: 2, allowEdit: false, hasWatermark: false}) {
        globalCallbackOfGetPicture = callback
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.getPicture('getPictureCallback', JSON.stringify(options))
        }

        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(options)
          window.location.href = `native:photo:getPictureCallback:${encoded}`
        }
      }
    },
    tel: {
      /**
       * 拨打电话
       * @param phone 电话号码
       */
      call (phone) {
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.call(phone)
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          window.location.href = `tel:${phone}`
        }
      }
    },
    location: {
      /**
       * 获取用户当前位置
       * @param callback
       */
      getCurrentPosition (callback) {
        globalCallbackOfCurrentPosition = callback
        if (bridgeUtil.Platform.isPlatform(PLATFORM_DESKTOP)) {
          callback({longitude: 116.40387397, latitude: 39.91488908})
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.getCurrentPosition('currentPositionCallback')
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          window.location.href = `native:position:currentPositionCallback:`
        }
      },
      /**
       * 查看地图
       * @param longitude 经度
       * @param latitude 纬度
       * @param address 地址
       */
      map (longitude, latitude, address) {
        let options = {
          longitude,
          latitude,
          address
        }

        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.map(JSON.stringify(options))
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(options)
          window.location.href = `native:map:${encoded}`
        }
      },
      /**
       * 导航
       * @param longitude 经度
       * @param latitude 纬度
       * @param address 地址
       */
      nav (longitude, latitude, address) {
        let options = {
          longitude,
          latitude,
          address
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.nav(JSON.stringify(options))
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(options)
          window.location.href = `native:nav:${encoded}`
        }
      }
    },
    /**
     * 支付功能
     */
    payment: {
      /**
       * 支付
       * @param params 支付时所需的参数
       */
      pay (params) {
        if (bridgeUtil.Platform.isPlatform(PLATFORM_ANDROID)) {
          window.appNative.pay(JSON.stringify(params))
        }
        if (bridgeUtil.Platform.isPlatform(PLATFORM_IOS)) {
          let encoded = bridgeUtil.Base64.getBase64String(params)
          window.location.href = `native:pay:${encoded}`
        }
      }
    }
  }
}

let bridgeUtil = {
  Platform: {
    isPlatform (platform) {
      platform = platform.toLowerCase()

      let baseInfo = appBridge.h5ToNative.getBaseInfo()

      let isWindows = navigator.platform === 'Win32' || navigator.platform === 'Windows'
      let isMac = navigator.platform === 'Mac68K' || navigator.platform === 'MacPPC' || navigator.platform === 'Macintosh' || navigator.platform === 'MacIntel'

      let isDesktop = isWindows || isMac || baseInfo.callType.toLowerCase() === PLATFORM_DESKTOP

      if (platform === PLATFORM_DESKTOP) {
        return isDesktop
      }
      if (platform === PLATFORM_ANDROID) {
        return !isDesktop && baseInfo.callType.toLowerCase() === PLATFORM_ANDROID
      }
      if (platform === PLATFORM_IOS) {
        return !isDesktop && baseInfo.callType.toLowerCase() === PLATFORM_IOS
      }

      return false
    }
  },
  Base64: {
    getBase64String (data) {
      if (typeof data === 'object') {
        data = JSON.stringify(data)
      }

      let bytes = utf8.encode(data)
      return base64.encode(bytes)
    }
  }
}

let tokenInvalid = (errorMessage) => {
  appBridge.h5ToNative.tokenInvalid(errorMessage)
}

let getBaseInfo = () => {
  return appBridge.h5ToNative.getBaseInfo()
}

export {tokenInvalid, getBaseInfo}

window.appBridge = appBridge.nativeToH5 // 向Webview容器暴露全局对象，供native调用
Vue.prototype.$appBridge = appBridge.h5ToNative

Vue.prototype.$Platform = bridgeUtil.Platform



