import Vue from 'vue'

const SIGN_REGEXP = /[yMdhsm]+/g

function paddingLeftZero (str) {
  str = str + ''
  return ('00' + str).substr(str.length)
}

/**
 * 格式化日期
 * @param date 日期对象
 * @param pattern 日期匹配模式 默认yyyy-MM-dd
 * @returns {string} 返回格式化后的日期字符串
 */
Vue.filter('formatDate', (date, pattern = 'MM-dd hh:mm') => {
  if (!date) {
    return ''
  }
  if (typeof (date) === 'string') {
    date = date.replace(/-/g, '/')
    date = new Date(date)
  }
  if (typeof (date) === 'number') {
    date = new Date(date)
  }
  return pattern.replace(SIGN_REGEXP, function (match) {
    switch (match.charAt(0)) {
      case 'y': return (date.getFullYear() + '').substr(4 - match.length)
      case 'M': return paddingLeftZero(date.getMonth() + 1)
      case 'd': return paddingLeftZero(date.getDate())
      case 'w': return date.getDay() + 1
      case 'h': return paddingLeftZero(date.getHours())
      case 'm': return paddingLeftZero(date.getMinutes())
      case 's': return paddingLeftZero(date.getSeconds())
    }
  })
})



