import 'babel-polyfill'

import Vue from 'vue'
import {Carousel, CarouselItem} from 'element-ui'

import App from './App'
/* eslint-disable no-new */

Vue.use(Carousel)
Vue.use(CarouselItem)

new Vue({
  el: '#app',
  template: '<App/>',
  components: {
    App
  }
})



