(function () {
  let docEl = document.documentElement
  let resizeEvent = 'orientationchange' in window ? 'orientationchange' : 'resize'
  let recalc = () => {
    let clientWidth = docEl.clientWidth
    if (!clientWidth) {
      return
    }

    // 设计稿宽度
    let designDraftWidth = 1920
    // 设计稿根元素
    let designDraftHtmlFontSize = 20

    if (clientWidth > 1920) {
      clientWidth = 1920
    }
    if (clientWidth < 1000) {
      clientWidth = 1000
    }
    let fontSizeRate = (clientWidth / designDraftWidth)
    let baseFontSize = designDraftHtmlFontSize * fontSizeRate
    docEl.style.fontSize = baseFontSize + 'px'
  }

  recalc()
  if (!document.addEventListener) {
    return
  }
  window.addEventListener(resizeEvent, recalc, false)
  document.addEventListener('DOMContentLoaded', recalc, false)
})()



