            title: '城际协同甩挂',
            content: '甩挂运输就是带有动力的牵引头把随车拖带的半挂车甩留在目的地后，再拖带其他装满货物的半挂车返回原地。我们与传统甩挂模式不同点在于出发地与目的地多点组织货源，更加确保双边货源稳定对等，从而实行城际之间的协同甩挂'
