            height: pxToRem(670);
            .contact-box{
              flex: 1;
              &.contact-map{
                position: relative;
                .image{
                  width: 100%;
                  height: 100%;
                }
              }
              &.contact-info{
                background: url('../../assets/map-bg.png') no-repeat;
                background-size: 100% 100%;
                .contact-list{
                  margin: pxToRem(36 88 0);
                  .contact-item{
                    display: flex;
                    margin-bottom: pxToRem(46);
                    &:last-child{
                      margin-bottom:0;
                    }
                    .contact-item-image{
                      width: pxToRem(52);
                      height: pxToRem(52);
                      margin-right:pxToRem(10);
                    }
                    .contact-item-content{
                      color: #333;
                      .title{
                        margin-bottom: pxToRem(10);
                        font-size: pxToRem(18);
                      }
                      .subtitle{
                        font-size: pxToRem(28);
                        &.address{
                          font-size: pxToRem(16);
                        }
                      }
                    }
                  }
                }
                .contact-downloads{
                  display: flex;
                  justify-content:space-around;
                  margin: pxToRem(50 0 40);
                  .contact-download{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .figure{
                      text-align: center;
                      .image{
                        width: pxToRem(180);
                        height: pxToRem(180);
                      }
                      .figcaption{
                        margin-top: pxToRem(30);
                        font-size: pxToRem(18);
                        color:#333;
                        text-align: center;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .footer{
      font-size: pxToRem(14);
      .info-list{
        margin: 0 auto;
        margin-top: pxToRem(50);
        width: pxToRem(1084);
        white-space: nowrap;
        background: $color-light;
        color:#999;
        .info-item{
          margin-bottom: pxToRem(20);
          padding-bottom: pxToRem(20);
          border-bottom: 1px solid #999;
          &:last-child{
            margin-bottom: 0;
            border-bottom: none;
          }
          &.info-item-link{
            display: flex;
            .label{
            }
            .links{
              display: flex;
              align-items: center;
              .link{
                padding: 0 pxToRem(5);
                border-right: 1px solid #999;
                line-height: 1;
                cursor: pointer;
                &:last-child{
                  border-right: 0;
                }
                a{
                  color: inherit;
                }
              }
            }
          }
          &.info-item-base{
            display: flex;
            justify-content: space-between;
            .info-item-box{
              div:first-child{
                margin-bottom: pxToRem(10);
              }
              .phone{
                color: $color-lively;
              }
              &.info-item-box-logo{
                align-self: flex-end;
              }
              .links{
                .link{
                  cursor: pointer;
                }
              }
            }
          }
        }
      }
      .info-copyright{
        display: flex;
        justify-content: center;
        align-items: center;
        height: pxToRem(70);
        background: #535353;
        color:#999;
        .info-copyright-img{
          margin-right: pxToRem(10);
        }
      }
    }
  }

</style>



